<h1 id="订单" tabindex="-1"><a class="header-anchor" href="#订单" aria-hidden="true">#</a> 订单</h1>
<h2 id="统一下单" tabindex="-1"><a class="header-anchor" href="#统一下单" aria-hidden="true">#</a> 统一下单</h2>
<p>没错，什么 H5 支付，公众号支付，扫码支付，支付中签约，全部都是用这个接口下单。</p>
<blockquote>
<p>{info} 参数 <code>appid</code>, <code>mch_id</code>, <code>nonce_str</code>, <code>sign</code>, <code>sign_type</code> 可不用传入</p>
</blockquote>
<blockquote>
<p>服务商模式下, 需使用 <code>sub_openid</code>, 并传入<code>sub_mch_id</code> 和<code>sub_appid</code></p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">order</span><span class="token operator">-></span><span class="token function">unify</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'body'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'腾讯充值中心-QQ会员充值'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'out_trade_no'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'20150806125346'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'total_fee'</span> <span class="token operator">=></span> <span class="token number">88</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'spbill_create_ip'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'123.12.12.123'</span><span class="token punctuation">,</span> <span class="token comment">// 可选，如不传该参数，SDK 将会自动获取相应 IP 地址</span>
    <span class="token string single-quoted-string">'notify_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'https://pay.weixin.qq.com/wxpay/pay.action'</span><span class="token punctuation">,</span> <span class="token comment">// 支付结果通知网址，如果不设置则会使用配置里的默认地址</span>
    <span class="token string single-quoted-string">'trade_type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'JSAPI'</span><span class="token punctuation">,</span> <span class="token comment">// 请对应换成你的支付方式对应的值类型</span>
    <span class="token string single-quoted-string">'openid'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'oUpF8uMuAJO_M2pxb1Q9zNjWeS6o'</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

