<p>该接口提供所有微信支付订单的查询，商户可以通过该接口主动查询订单状态，完成下一步的业务逻辑。</p>
<p>需要调用查询接口的情况：</p>
<blockquote>
<ul>
<li>当商户后台、网络、服务器等出现异常，商户系统最终未接收到支付通知；</li>
<li>调用支付接口后，返回系统错误或未知交易状态情况；</li>
<li>调用被扫支付 API，返回 USERPAYING 的状态；</li>
<li>调用关单或撤销接口 API 之前，需确认支付状态；</li>
</ul>
</blockquote>
<h3 id="根据商户订单号查询" tabindex="-1"><a class="header-anchor" href="#根据商户订单号查询" aria-hidden="true">#</a> 根据商户订单号查询</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">order</span><span class="token operator">-></span><span class="token function">queryByOutTradeNumber</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"商户系统内部的订单号（out_trade_no）"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="根据微信订单号查询" tabindex="-1"><a class="header-anchor" href="#根据微信订单号查询" aria-hidden="true">#</a> 根据微信订单号查询</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">order</span><span class="token operator">-></span><span class="token function">queryByTransactionId</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"微信订单号（transaction_id）"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="关闭订单" tabindex="-1"><a class="header-anchor" href="#关闭订单" aria-hidden="true">#</a> 关闭订单</h2>
<blockquote>
<p>{warning} 注意：订单生成后不能马上调用关单接口，最短调用时间间隔为5分钟。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">order</span><span class="token operator">-></span><span class="token function">close</span><span class="token punctuation">(</span>商户系统内部的订单号（out_trade_no）<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>